.category_item {
  border: 1px solid grey;
  flex: 1;
  border-radius: 5px;
}

.link {
  text-decoration: none;
}

.category_item img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  display: block;
}

.category_item h3 {
  background-color: #fff;
  padding: 5px;
  color: #006496;
}